    p(data-aos="fade-down").mt-5 La media, la mediana y la moda son tres tipos de promedios. Hay muchos promedios en estadstica, pero estos son los tres ms comunes y, ciertamente, son los tres ms utilizados al momento de llevar a cabo aplicaciones y procesos estadsticos.

