              h3.mb-0.text-white.text-center #[b Mediana]
          p.my-5 La mediana es la observacin intermedia en un conjunto de datos. Para contextualizar qu significa, se calcula la mediana de un conjunto de datos de muestra sobre el peso infantil, as:

          img.img-a.img-t(data-aos="zoom-in")(src='@/assets/curso/temas/img16.svg', alt='')
          p.my-5 En el siguiente conjunto de datos, cul es la observacin intermedia? Bueno, antes que pueda resolver esto, tenemos que ordenar correctamente las observaciones de una manera lgica para que tengan sentido. Los ordenaremos de menor a mayor, como se muestra a continuacin:

          .row.justify-content-center.align-items-center.mt-5           
