              .cajon.color-primario.p-4.mt-5
                p.mb-0 #[b Ahora que nuestros datos estn ordenados correctamente, podemos encontrar la observacin intermedia.]

          .row.justify-content-center.align-items-center.mt-5           
            .col-lg-7
              LineaTiempoD.color-acento-contenido
                .row.justify-content-center.align-items-center(numero="1" titulo="Nmero impar de observaciones")
                  .col-11
                    p En un conjunto de datos que tiene un nmero impar de observaciones, esto es muy fcil; es simplemente el nmero justo en el medio (el que tiene el mismo nmero de observaciones arriba y abajo). 
                    img.img-a.img-t.mb-3.mt-4(src='@/assets/curso/temas/img20.svg', alt='')
                    img.img-a.img-t(src='@/assets/curso/temas/img21.svg', alt='')

                .row.justify-content-center.align-items-center(numero="2" titulo="Nmero par de observaciones")
                  .col-11
                    p Sin embargo, en nuestro caso tenemos 12 observaciones, que es un nmero par. Esto significa que debemos tomar las dos observaciones del centro y promediarlas. En este caso, las dos observaciones en el medio son 42 y 49. Cuando tomamos el promedio de estos dos nmeros (para hacer un promedio, sume los dos nmeros (42 + 49 = 91) y divida ese nmero por la cuenta, que en este caso es 2), obtenemos 45,5. Entonces nuestra mediana es 45,5. 
                    img.img-a.img-t.mb-3.mt-4(src='@/assets/curso/temas/img22.svg', alt='')
                    img.img-a.img-t(src='@/assets/curso/temas/img23.svg', alt='')

                .row.justify-content-center.align-items-center(numero="3" titulo="Entonces, qu significa la mediana?")
                  .col-11
                    p Bueno, al igual que la media, proporciona una medida til del centro de nuestro conjunto de datos. Ahora sabemos que el peso medio de los nios de nuestro grupo es de 45,5. Pero tambin es til comparar la mediana con la media. 45,5 es obviamente menor que la media, que fue 53,5. A menudo, la media y la mediana sern las mismas en un conjunto de datos, pero a veces son diferentes, como en nuestro caso. Cuando la media y la mediana son iguales, se sabe que el conjunto de datos est distribuido normalmente. Cuando la media y la mediana son diferentes, se sabe que los datos estn sesgados de alguna manera.

                    img.img-a.img-t.mt-4(src='@/assets/curso/temas/img24.svg', alt='')

                .row.justify-content-center.align-items-center(numero="4" titulo="Qu se quiere decir con sesgado?")
                  .col-11
                    p Bueno, a diferencia de la media, que era un clculo matemtico que usaba todas las observaciones del conjunto de datos, la mediana ignora lo que dicen los nmeros y solo usa la observacin del medio. Cul es la correcta? Ambos lo son. Ninguno es necesariamente mejor que el otro. Entonces, por qu usar una mediana? Bueno, hay ciertos tipos de datos en los que le preocupa el sesgo que  es cuando la media sube o baja por encima de la mediana debido a valores muy altos o muy bajos. 
 
                    img.img-a.img-t.mt-4(src='@/assets/curso/temas/img25.svg', alt='')
