              h3.mb-0.text-center #[b Moda]
          p.my-5 Es el valor que aparece con mayor frecuencia en un conjunto de valores de datos. Si X es una variable aleatoria discreta, la moda es el valor x (es decir, X = x) en el que la funcin de masa de probabilidad toma su valor mximo. En otras palabras, es el valor que es ms probable que se muestree. De esta manera:
          .bg-gradi.px-5
            .bloque-texto-g.color-secundario.p-3.p-sm-4.p-md-5
              .bloque-texto-g__img(
                :style="{'background-image': `url(${require('@/assets/curso/temas/img23.jpg')})`}"
              )
              .bloque-texto-g__texto.p-4
                p.mb-0 Al igual que la media y la mediana estadsticas, la moda es una forma de expresar, en un nmero (normalmente) nico, informacin importante sobre una variable aleatoria o una poblacin. El valor numrico de la moda es el mismo que el de la media y la mediana en una distribucin normal y puede ser muy diferente en distribuciones muy asimtricas. La moda no es necesariamente nica para una distribucin discreta dada, ya que la funcin de masa de probabilidad puede tomar el mismo valor mximo en varios puntos x1 x2, etc. El caso ms extremo ocurre en distribuciones uniformes, donde todos los valores ocurren con la misma frecuencia.
            .tarjeta.tarjeta--azul.bg-slyder1.p-4.mt-5                
              SlyderA
                .row.justify-content-center.align-items-center
                  .col-lg-5.imgs-flotantes2                        
                    .color-fondo-text.bg-color
                      .color-fondo-text__texto        
                        p Cuando la funcin de densidad de probabilidad de una distribucin continua tiene mltiples mximos locales, es comn referirse a todos los mximos locales como modos de la distribucin. Esta distribucin continua se denomina multimodal (en contraposicin a unimodal). Un modo de una distribucin de probabilidad continua se considera a menudo como cualquier valor x en el que su funcin de densidad de probabilidad tiene un valor mximo localmente, por lo que cualquier pico es un modo.
                      img.img-flotante2.d-none.d-lg-block.mt-5(src='@/assets/curso/temas/sobre.svg', alt='Imagen decorativa')                         
                  .col-lg-5.my-3      
                    img.img-a.img-t.mt-4(src='@/assets/curso/temas/img26.svg', alt='')

                .row.justify-content-center.align-items-center
                  .col-lg-5.my-3      
                    img.img-a.img-t.mt-4(src='@/assets/curso/temas/img26_1.svg', alt='')                  
                  .col-lg-5.imgs-flotantes2                       
                    .color-fondo-text2.bg-color
                      .color-fondo-text2__texto        
                        p En distribuciones unimodales simtricas, como la distribucin normal, la media (si est definida), la mediana y la moda coinciden. Para las muestras, si se sabe que se extraen de una distribucin unimodal simtrica, la media de la muestra se puede utilizar como una estimacin de la moda poblacional.
                      img.img-flotante2.d-none.d-lg-block.mt-3(src='@/assets/curso/temas/sobre.svg', alt='Imagen decorativa') 
                .row.justify-content-center.align-items-center
                  .col-lg-5.imgs-flotantes2                        
                    .color-fondo-text3.bg-color
                      .color-fondo-text3__texto        
                        p Moda de una muestra: la moda de una muestra es el elemento que aparece con mayor frecuencia en la coleccin. Por ejemplo, la moda de la muestra [1, 3, 6, 6, 6, 6, 7, 7, 12, 12, 17] es 6. Dada la lista de datos [1, 1, 2, 4, 4] su modo no es nico. En tal caso, se dice que un conjunto de datos es bimodal, mientras que un conjunto con ms de dos modos puede describirse como multimodal.
                      img.img-flotante2.d-none.d-lg-block.mt-4(src='@/assets/curso/temas/sobre.svg', alt='Imagen decorativa') 
                  .col-lg-5.my-3      
                    img.img-a.img-t.mt-4(src='@/assets/curso/temas/img26_2.svg', alt='')

                .row.justify-content-center.align-items-center
                  .col-lg-5.my-3      
                    img.img-a.img-t.mt-4(src='@/assets/curso/temas/img26_3.svg', alt='')                  
                  .col-lg-5.imgs-flotantes2                        
                    .color-fondo-text4.bg-color
                      .color-fondo-text4__texto        
                        p Para una muestra de una distribucin continua, como [0.935 ..., 1.211 ..., 2.430 ..., 3.668 ..., 3.874 ...], el concepto es inutilizable en su forma bruta, ya que no hay dos valores ser exactamente el mismo, por lo que cada valor ocurrir exactamente una vez. Para estimar la moda de la distribucin subyacente, la prctica habitual es discretizar los datos asignando valores de frecuencia a intervalos de igual distancia, como para hacer un histograma, reemplazando efectivamente los valores por los puntos medios de los intervalos a los que estn asignados. El modo es, entonces, el valor en el que el histograma alcanza su pico. 
                      img.img-flotante2.d-none.d-lg-block.mt-3(src='@/assets/curso/temas/sobre.svg', alt='Imagen decorativa')                       

                .row.justify-content-center.align-items-center.pb-5
                  .col-lg-5.imgs-flotantes2                      
                    .color-fondo-text5.bg-color
                      .color-fondo-text5__texto        
                        p.mb-5 Para muestras pequeas o medianas, el resultado de este procedimiento es sensible a la eleccin del ancho del intervalo si se elige demasiado estrecho o demasiado ancho; por lo general, se debe tener una fraccin considerable de los datos concentrada en un nmero relativamente pequeo de intervalos (5 a 10), mientras que la fraccin de los datos que quedan fuera de estos intervalos tambin es considerable. Un enfoque alternativo es la estimacin de la densidad del ncleo, que esencialmente difumina las muestras puntuales para producir una estimacin continua de la funcin de densidad de probabilidad que puede proporcionar una estimacin del modo.
                      img.img-flotante2.d-none.d-lg-block.mt-5(src='@/assets/curso/temas/sobre.svg', alt='Imagen decorativa')   
                  .col-lg-5.my-3      
                    img.img-a.img-t.mt-4(src='@/assets/curso/temas/img26_4.svg', alt='')
                  
