          p.mt-5 A diferencia de la media y la mediana, el concepto de moda tambin tiene sentido para los datos nominales (es decir, que no constan de valores numricos en el caso de la media, ni siquiera de valores ordenados en el caso de la mediana). Por ejemplo, tomando una muestra de apellidos coreanos, uno podra encontrar que Kim aparece con ms frecuencia que cualquier otro nombre. Entonces Kim sera la moda de la muestra. En cualquier sistema de votacin donde una pluralidad determina la victoria, un nico valor modal determinar al vencedor, mientras que un resultado multimodal requerira algn procedimiento de desempate.

          img.img-a.img-t.mt-4(src='@/assets/curso/temas/img27.svg', alt='')

          .cajon.color-primario.p-4.mt-5
            p.mb-0 A diferencia de la mediana, el concepto de moda tiene sentido para cualquier variable aleatoria que asuma valores de un espacio vectorial, incluidos los nmeros reales (un espacio vectorial unidimensional) y los enteros (que pueden considerarse incrustados en los reales). Por ejemplo, una distribucin de puntos en el plano normalmente tendr una media y una moda, pero el concepto de mediana no se aplica. La mediana tiene sentido cuando existe un orden lineal en los valores posibles. 

