            .bloque-texto-g__texto.p-4
              p.mb-0 La varianza es la expectativa de la desviacin al cuadrado de una variable aleatoria de su media poblacional o muestral. La varianza es una medida de dispersin, lo que significa que es una medida de qu tan lejos se separa un conjunto de nmeros de su valor promedio y se define de manera detallada as:

          .row.justify-content-start.align-items-center.mt-5     
