              LineaTiempoD.color-acento-contenido
                .row(numero="1" titulo="Varianza y desviacin tpica o estndar")
                  .col-11
                    p La varianza es la expectativa de la desviacin al cuadrado de una variable aleatoria de su media poblacional o muestral. La varianza es una medida de dispersin, lo que significa que es una medida de qu tan lejos se separa un conjunto de nmeros de su valor promedio. La varianza tiene un papel central en la estadstica, donde algunas ideas que la usan incluyen estadstica descriptiva, inferencia estadstica, prueba de hiptesis y muestreo de Monte Carlo. 
                    img.img-a.img-t.mt-4(src='@/assets/curso/temas/img29.svg', alt='')

                .row(numero="2" titulo="La varianza")
                  .col-11
                    p Es una herramienta importante en las ciencias, donde el anlisis estadstico de datos es comn. La varianza es el cuadrado de la desviacin estndar, el segundo momento central de una distribucin y la covarianza de la variable aleatoria consigo misma, y a menudo se representa por 2, s2, Var(x) o V(x).

                    img.img-a.img-t.mt-4(src='@/assets/curso/temas/img30.svg', alt='')                    
              
                .row(numero="3" titulo="Una ventaja de la varianza como medida de dispersin es")
                  .col-11
                    p Ms susceptible de manipulacin algebraica que otras medidas de dispersin, como la desviacin absoluta esperada; por ejemplo, la varianza de una suma de variables aleatorias no correlacionadas es igual a la suma de sus varianzas. Una desventaja de la varianza para aplicaciones prcticas es que, a diferencia de la desviacin estndar, sus unidades difieren de la variable aleatoria, por lo que la desviacin estndar se informa ms comnmente como una medida de dispersin una vez finalizado el clculo.

                    img.img-a.img-t.mt-4(src='@/assets/curso/temas/img31.svg', alt='')               

                .row(numero="4" titulo="La desviacin estndar en las estadsticas")
                  .col-11
                    p Est tpicamente denotada por , es una medida de variacin o dispersin (se refiere al grado de estiramiento o compresin de una distribucin) entre valores en un conjunto de datos. Cuanto menor es la desviacin estndar, ms cerca tienden a estar los puntos de datos de la media (o valor esperado), . Por el contrario, una desviacin estndar ms alta indica una gama ms amplia de valores. De manera similar a otros conceptos matemticos y estadsticos, existen muchas situaciones diferentes en las que se puede usar la desviacin estndar y, por lo tanto, muchas ecuaciones diferentes. 

                    img.img-a.img-t.mt-4(src='@/assets/curso/temas/img32.svg', alt='')
                    
                    .row.justify-content-start.align-items-center.ps-3.mt-4        
