                        p.mb-0.text-white #[b Adems de expresar la variabilidad de la poblacin]   

                    p.mt-4 La desviacin estndar tambin se usa a menudo para medir resultados estadsticos como el margen de error. Cuando se usa de esta manera, la desviacin estndar a menudo se denomina error estndar de la media o error estndar de la estimacin con respecto a una media. 
 
                .row(numero="5" titulo="Desviacin estndar de poblacin")
                  .col-11
                    p La desviacin estndar de la poblacin, la definicin estndar de  se usa cuando se puede medir una poblacin completa y es la raz cuadrada de la varianza de un conjunto de datos dado.

                    p #[b En los casos en que se pueda muestrear a cada miembro de una poblacin], se puede utilizar la siguiente ecuacin para encontrar la desviacin estndar de toda la poblacin:

                    img.img-a.img-t.mt-4(src='@/assets/curso/temas/img33.svg', alt='')

                    p.mt-4 #[b Para aquellos] que no estn familiarizados con la notacin de suma, la ecuacin anterior puede parecer desalentadora, pero cuando se aborda a travs de sus componentes individuales, esta suma no es particularmente complicada. 

                    .row.justify-content-start.align-items-center.ps-3.mt-4        
                      .col-lg-5.green-p.p-3(data-aos="fade-down")
                        p(data-aos="fade-down").mb-0.text-white #[b Para entender ms] 

                    p.mt-4 El i = 1 en la suma indica el ndice inicial, es decir, para el conjunto de datos 1, 3, 4, 7, 8, i = 1 sera 1, i = 2 sera 3, y as sucesivamente. Por lo tanto, la notacin de suma simplemente significa realizar la operacin de (xi  )2 en cada valor a travs de N, que en este caso es 5 ya que hay 5 valores en este conjunto de datos.

                    h4 Ejemplo:

                    p  = (1 + 3 + 4 + 7 + 8) / 5 = 4,6.       
                    p  =  [(1  4,6)2 + (3  4,6)2 +  + (8  4,6)2] / 5.
                    p  =  (12,96 + 2,56 + 0,36 + 5,76 + 11,56) / 5 = 2,577.

                .row(numero="6" titulo="Desviacin estndar de la muestra")
                  .col-11
                    p En muchos casos, no es posible muestrear a todos los miembros dentro de una poblacin, lo que requiere que se modifique la ecuacin anterior para que la desviacin estndar se pueda medir a travs de una muestra aleatoria de la poblacin que se est estudiando. Un estimador comn para  es la desviacin estndar de la muestra, normalmente denotada por s. Vale la pena sealar que existen muchas ecuaciones diferentes para calcular la desviacin estndar de la muestra, ya que, a diferencia de la media de la muestra, la desviacin estndar de la muestra no tiene un estimador nico que sea insesgado, eficiente y con una probabilidad mxima.

                    img.img-a.img-t.mt-4(src='@/assets/curso/temas/img34.svg', alt='')

                    .cajon.color-primario.p-4.mt-4
                      p.mb-0 #[b La ecuacin proporcionada a continuacin es] la desviacin estndar de la muestra corregida. Es una versin corregida de la ecuacin obtenida al modificar la ecuacin de desviacin estndar de la poblacin utilizando el tamao de la muestra como el tamao de la poblacin, lo que elimina parte del sesgo de la ecuacin. Sin embargo, la estimacin imparcial de la desviacin estndar es muy complicada y vara segn la distribucin.
 
                    img.img-a.img-t.mt-4(src='@/assets/curso/temas/img35.svg', alt='')

                    p.mt-4 #[b Como tal] la desviacin estndar de la muestra corregida es el estimador ms comnmente utilizado para la desviacin estndar de la poblacin, y generalmente se la denomina simplemente desviacin estndar de la muestra. Es una estimacin mucho mejor que su versin sin corregir, pero an tiene un sesgo significativo para tamaos de muestra pequeos (N <10).

                    img.img-a.img-t.mt-4(src='@/assets/curso/temas/img36.svg', alt='')

