          .cajon.color-primario.p-4.mt-5
            p.mb-0 La desviacin estndar se usa ampliamente en entornos experimentales e industriales para probar modelos con datos del mundo real; un ejemplo de esto es el control de calidad de algunos productos. A continuacin, se informa acerca de sus usos:

          .row.justify-content-start.align-items-center.ps-3.mt-5         
            .col-lg-4.green-p.p-3
              p.mb-0.text-white #[b Aplicaciones de la desviacin estndar]

          .row.justify-content-center.align-items-center.mt-5     
            .col-lg-4              
              img.img-a.img-t(src='@/assets/curso/temas/img38.svg', alt='')
            .col-lg-8 
              p La desviacin estndar se puede utilizar para calcular un valor mnimo y mximo dentro del cual algn aspecto del producto debera caer en un alto porcentaje del tiempo. En los casos en que los valores estn fuera del rango calculado, puede ser necesario realizar cambios en el proceso de produccin para garantizar el control de calidad.
              
              .cajon.color-primario.p-4.mt-5.mx-4
                p.mb-0 La desviacin estndar tambin se usa en el clima para determinar las diferencias en el clima regional. Imagnese dos ciudades, una en la costa y otra tierra adentro, que tienen la misma temperatura media de 75 F. Si bien esto puede generar la creencia de que las temperaturas de estas dos ciudades son prcticamente las mismas, la realidad podra enmascararse si solo se aborda la media y se ignora la desviacin estndar. 

          .bloque-texto-a.color-secundario.p-4.p-md-5.mt-5 
            .row.m-0.align-items-center.justify-content-between
              .col-lg-12
                .bloque-texto-a__texto.p-4
                  p.mb-0 Las ciudades costeras tienden a tener temperaturas mucho ms estables debido a la regulacin de grandes masas de agua, ya que el agua tiene una mayor capacidad calorfica que la Tierra; esencialmente, esto hace que el agua sea mucho menos susceptible a los cambios de temperatura, y las reas costeras permanecen ms clidas en invierno y ms frescas en verano debido a la cantidad de energa requerida para cambiar la temperatura del agua. 
        
          .row.justify-content-center.align-items-center.mt-5     
            .col-lg-2.my-2              
              img.img-a.img-t(src='@/assets/curso/temas/img39.svg', alt='')
            .col-lg-10 
              h5.mb-0 Otra rea en la que se usa ampliamente la desviacin estndar es la de las finanzas, donde a menudo se usa para medir el riesgo asociado en las fluctuaciones de precios de algn activo o cartera de activos.
 

    ModalA(:abrir-modal.sync="modal5")
      .row.align-items-center
