          .row.justify-content-start.align-items-center     
            .col-lg-4.bg-modal-p1.p-3
              h3.mb-0.text-center #[b Estudio de variables continuas]

          p.my-5 Se invita a realizar  un recorrido por #[b el estudio de las variables] continuas, as:

          h5 Una variable continua es un tipo especfico de variable cuantitativa que se utiliza en estadstica para describir datos que se pueden medir de alguna manera. Si sus datos tratan de medir la altura, el peso o el tiempo, entonces tiene una variable continua.

          .row.justify-content-center.align-items-center.mt-5     
            .col-lg-3.my-3              
              img.img-a.img-t(src='@/assets/curso/temas/img40.svg', alt='')
            .col-lg-9
              .cajon.color-primario.p-4
                p.mb-0 Definamos ms un par de trminos utilizados en nuestra definicin. Una variable en estadstica no es lo mismo que una variable en lgebra. En estadstica, una variable es algo que nos da datos. Algunos ejemplos de variables en las estadsticas pueden incluir edad, color de ojos, altura, nmero de hermanos, sexo o nmero de mascotas. Nuestra definicin de variable continua tambin menciona que es cuantitativa. Los datos cuantitativos involucran cantidades o nmeros. En los ejemplos de variables enumerados anteriormente, su edad, altura, nmero de hermanos y nmero de mascotas son todas variables cuantitativas.
 
          .row.justify-content-center.align-items-stretch.mt-5     
            .col-lg-6.col-bg-1.p-5
              img.img-t(src='@/assets/curso/temas/img41.svg', alt='')
              p.mt-4 #[b Datos]
              p.mb-0 La definicin tambin menciona que los datos se pueden medir de alguna manera. Para comprender esto, debe comprender las variables discretas. Los datos se consideran discretos si son un recuento. Cuando contamos cosas, usamos nmeros enteros como 0, 1, 2 y 3. Mi ejemplo favorito de una variable discreta es cuntos huevos pone una gallina. Cada da una gallina puede o no poner un huevo, pero hay dos cosas que nunca pueden suceder. Nunca puede haber un nmero negativo de huevos, y nunca puede haber una fraccin o una porcin de un huevo.
            .col-lg-6.col-bg-2.p-5
              img.img-t(src='@/assets/curso/temas/img42.svg', alt='')
              p.mt-4 #[b Variable]
              p.mb-0 Las variables continuas son variables que miden algo. Un buen ejemplo de variable continua es cuntos galones de leche da una vaca. Hasta donde se sabe, las vacas no saben cmo dejar de producir o dar leche despus de exactamente 4 galones. Bessie puede dar 4,17 galones el lunes, 3,89 galones el martes y 4,2 galones el mircoles. Observe que las variables continuas nos permiten tener decimales o fracciones.

    Separador 
    #t_1_2.titulo-segundo.color-acento-contenido(data-aos="zoom-in-left")
      h2 1.2 Distribuciones bidimensionales, diagramas de dispersin y rectas de regresin

    p(data-aos="fade-down") La distribucin bidimensional es una representacin grfica de un conjunto de puntos dentro de un diagrama de dispersin que representan individuos dentro de la misma distribucin y las rectas de regresin son un mtodo para definir la relacin que existe entre estos puntos o el comportamiento de los mismos.
    .row.justify-content-start.align-items-center.mt-5(data-aos="fade-right")
      .col-lg-4.bg-modal-p2.p-3
        h3.mb-0.text-white #[b Distribuciones Bidimensionales]
    .row.justify-content-center.align-items-center.mt-5           
      .col-lg-4.my-3 
        img.img-a.img-t(data-aos="zoom-in")(src='@/assets/curso/temas/img106.svg', alt='')

      .col-lg-8 
        p(data-aos="fade-down") En la estadstica, a la distribucin donde interceden dos variables, x e y, se le denomina distribucin bidimensional, esto significa que a cada individuo le pertenecen dos valores, xi, yi. Y al momento de ser representados en un plano o diagrama cartesiano se les debe considerar como coordenadas de un solo punto (xi, yi). As, toda esta distribucin se visualizar como un conjunto de puntos y cada punto corresponde a un individuo de la distribucin.   
        .cajon.color-primario.p-4.mt-4(data-aos="flip-down")
          p(data-aos="fade-down").mb-0 Como ejemplo tpico de distribucin bidimensional, se pueden tomar los errores de precisin del fuego de artillera. El error total se compone de dos desviaciones independientes: error en la distancia de disparo X y desviacin lateral Y de la direccin de disparo. Un nmero tan relativamente pequeo de puntos clasifica esta distribucin entre las distribuciones discretas.

    .row.justify-content-start.align-items-center.mt-5(data-aos="fade-right")
      .col-lg-2.bg-modal-p2.p-3
        h3.mb-0.text-white #[b Correlacin]

    .row.justify-content-center.align-items-center.mt-5
      .col-lg-8
        p(data-aos="fade-down") La correlacin es una medida estadstica que expresa el grado en que dos variables estn relacionadas linealmente (lo que significa que cambian juntas a una tasa constante). Es una herramienta comn para describir relaciones simples sin hacer una declaracin sobre causa y efecto. La correlacin se mide mediante un coeficiente llamado coeficiente de correlacin de la muestra, r, la cual cuantifica la fuerza de la relacin. Las correlaciones tambin se prueban para determinar la significancia estadstica.

        .cajon.color-acento-botones.p-4.mt-4(data-aos="flip-down")
          p(data-aos="fade-down").mb-0 Las limitaciones de correlacin es que esta no puede considerar la presencia o el efecto de otras variables fuera de las dos que se estn explorando. Es importante destacar que la correlacin no nos dice sobre causa y efecto. La correlacin tampoco puede describir con precisin las relaciones curvilneas.

      .col-lg-4.my-3.imgs-flotantes
        img.img-a.img-t(data-aos="zoom-in")(src='@/assets/curso/temas/img107.jpg', alt='')
        img.img-flotante(data-aos="zoom-in")(src='@/assets/curso/temas/box.svg', alt='Imagen decorativa')     

    .bloque-texto-a.color-secundario.p-4.p-md-5.mt-5(data-aos="fade-down") 
      .row.m-0.align-items-center.justify-content-between
        .col-lg-12
          .bloque-texto-a__texto.p-4
            p.mb-0 Las correlaciones son tiles para describir relaciones simples entre datos. Por ejemplo, imagine que est mirando un conjunto de datos de campings en un parque de montaa. Quiere saber si existe una relacin entre la elevacin del camping (qu tan alto est la montaa) y la temperatura media alta en verano. Para cada campamento individual, tiene dos medidas: elevacin y temperatura. Cuando compara estas dos variables en su muestra con una correlacin, puede encontrar una relacin lineal: a medida que aumenta la elevacin, la temperatura desciende. Estn correlacionados negativamente.

    AcordionA.mt-5(tipo="a" clase-tarjeta="tarjeta tarjeta--azul")(data-aos="fade-right")
      .row.justify-content-center.align-items-center.pb-4(titulo="Diagramas de dispersin")
        .col-11
          .row.justify-content-center.align-items-center         
            .col-lg-4.my-3
              img.img-a.img-t(src='@/assets/curso/temas/img43.svg', alt='')
            .col-lg-8
              p Estas son herramientas matemticas convenientes para estudiar la correlacin entre dos variables aleatorias. Como su nombre indica, son una forma de hoja de papel sobre la que se encuentran dispersos los puntos de datos correspondientes a las variables de inters:            
              .cajon.color-primario.p-4.mt-4
                p.mb-0 A juzgar por la forma del patrn que forman los puntos de datos en esta hoja de papel, podemos determinar la asociacin entre las dos variables y aplicar la tcnica de anlisis de correlacin ms adecuada.         

          .titulo-sexto.color-acento-contenido.mt-5
            h5 Figura 2.
            span a. Correlacin dbil. b. Correlacin fuerte. c. Correlacin inversa. 
          img.img-a.img-t.mt-3(src='@/assets/curso/temas/img44.svg', alt='')

          .row.justify-content-start.align-items-center.ps-3.mt-5         
            .col-lg-4.green-p.p-3
              p.mb-0.text-white #[b Interpretacin de diagramas de dispersin] 

          .row.justify-content-center.align-items-center.mt-5
            .col-lg-10
              p Los diagramas de dispersin entre dos variables aleatorias presentan las variables como sus ejes x e y. Podemos tomar cualquier variable como variable independiente en tal caso (la otra variable es la dependiente) y, en consecuencia, graficar cada punto de datos en el grfico (xi, yi). La totalidad de todos los puntos graficados forma el diagrama de dispersin. Basndonos en las diferentes formas que puede asumir el diagrama de dispersin, podemos sacar diferentes inferencias. Podemos calcular un coeficiente de correlacin para los datos dados. Es una medida cuantitativa de la asociacin de las variables aleatorias. Su valor es siempre menor que 1 y puede ser positivo o negativo. 
            .col-lg-2.my-3
              img.img-a.img-t(src='@/assets/curso/temas/img45.svg', alt='')    

          .row.justify-content-center.align-items-stretch.mt-5     
            .col-lg-6.col-bg-1b.p-5
              img.img-t(src='@/assets/curso/temas/img41.svg', alt='')
              p.mt-4 #[b Correlacin positiva]
              p.mb-0 En el caso de una correlacin positiva, los puntos graficados se distribuyen desde la esquina inferior izquierda a la esquina superior derecha (en el patrn general de estar distribuidos uniformemente sobre una lnea recta con una pendiente positiva); y en el caso de una correlacin negativa, los puntos trazados se extienden sobre una lnea recta de una pendiente negativa) desde la parte superior izquierda hacia la parte inferior derecha.

            .col-lg-6.col-bg-2b.p-5
              img.img-t(src='@/assets/curso/temas/img46.svg', alt='')
              p.mt-4 #[b Puntos distribuidos]
              p.mb-0 Si los puntos estn distribuidos aleatoriamente en el espacio, o distribuidos casi por igual en todos los lugares sin representar ningn patrn en particular, es el caso de una correlacin muy pequea, que tiende a 0.
       
          .row.justify-content-start.align-items-center.ps-3.mt-5         
            .col-lg-2.green-p.p-3
              p.mb-0.text-white #[b Regresin] 
              
          .row.justify-content-center.align-items-center         
            .col-lg-4.my-3
              img.img-a.img-t(src='@/assets/curso/temas/img47.svg', alt='')
            .col-lg-8
              p Consiste en una tcnica estadstica para estimar las relaciones entre variables. Incluye muchas tcnicas para modelar y analizar varias variables cuando el foco est en la relacin entre una variable dependiente y una o ms variables independientes. Ms especficamente, el anlisis de regresin ayuda a comprender cmo cambia el valor tpico de la variable dependiente cuando se vara cualquiera de las variables independientes, mientras que las otras variables independientes se mantienen fijas. Ms comnmente, el anlisis de regresin estima la expectativa condicional de la variable dependiente dadas las variables independientes, es decir, el valor promedio de la variable dependiente cuando las variables independientes son fijas. Con menos frecuencia, la atencin se centra en un cuantil, u otro parmetro de ubicacin de la distribucin condicional de la variable dependiente dadas las variables independientes. 
          
              .cajon.color-primario.p-4.mt-4
                p.mb-0 En todos los casos, el objetivo de estimacin es una funcin de las variables independientes, llamada funcin de regresin. En el anlisis de regresin, tambin es interesante caracterizar la variacin de la variable dependiente alrededor de la funcin de regresin, que puede describirse mediante una distribucin de probabilidad. Una aplicacin muy comn de la regresin es ayudar a los administradores financieros y de inversiones a valorar los activos y comprender las relaciones entre las variables, como los precios de los productos bsicos y las acciones de las empresas que comercian con esos productos bsicos.

          img.img-a.img-t.mt-5(src='@/assets/curso/temas/img48.svg', alt='')

          .bloque-texto-a.color-acento-botones.p-4.p-md-5.mt-5 
            .row.m-0.align-items-center.justify-content-between
              .col-lg-12
                .bloque-texto-a__texto.p-4
                  p.mb-0 El <em>software</em> no es solo para buscar filtros de caras divertidas o aprender nuevos movimientos de baile. Puede ayudar a su equipo a aumentar su eficiencia y ser ms productivo y capaz en su trabajo. Una solucin de <em>software</em> personalizada puede ayudar a eliminar el cuello de botella del seguimiento de los recibos y gastos del personal, o puede facilitar que su equipo administre los contactos de <em>marketing</em>.
 
          .row.justify-content-center.align-items-center.mt-5     
            .col-lg-2.my-2              
              img.img-a.img-t(src='@/assets/curso/temas/img49.svg', alt='')
            .col-lg-10 
              h5.mb-0 Y el <em>software</em> personalizado tambin puede servir como una solucin para sus clientes. Por ejemplo, una opcin de chat en vivo en su sitio web proporciona un contacto inmediato para clientes o prospectos con una necesidad urgente o que estn listos para realizar una compra. Un <em>software</em> personalizado tambin podra ayudar a sus clientes a rastrear a su representante de servicio en el camino a su hogar, o enviar una solicitud de soporte tcnico.
 
      .row.justify-content-center.align-items-center.pb-4(titulo="Rectas de regresin")
        .col-11
          p Estn definidas de la siguiente manera:
          .row.justify-content-between.align-items-stretch.mt-5.col-color-4     
            .col-lg-1.col-color-3.mx-0              
              img.img-a.img-t.mt-4(src='@/assets/curso/temas/a.svg', alt='')
            .col-lg-11.mx-0.p-4
              p.mb-0  Las rectas de regresin son lneas que se utiliza para describir el comportamiento de un conjunto de datos. En otras palabras, da la mejor tendencia de los datos proporcionados. Las rectas de regresin son tiles en los procedimientos de pronstico. Su propsito es describir la interrelacin de la variable dependiente (variable y) con una o muchas variables independientes (variable x). 
              img.img-a.img-t.mt-4(src='@/assets/curso/temas/img50.svg', alt='')

          .row.justify-content-between.align-items-stretch.mt-5.col-color-4     
            .col-lg-1.col-color-3.mx-0              
              img.img-a.img-t.mt-4(src='@/assets/curso/temas/b.svg', alt='')
            .col-lg-11.mx-0.p-4
              p.mb-0 El uso de la ecuacin obtenida de la recta de regresin acta como un analista que puede pronosticar comportamientos futuros de las variables dependientes ingresando diferentes valores para las independientes. Los dos tipos bsicos de regresin son la recta de regresin normal o simple y la recta de regresin mltiple, aunque existen mtodos de regresin no lineal para datos y anlisis ms complicados. 
 

          .row.justify-content-between.align-items-stretch.mt-5.col-color-4     
            .col-lg-1.col-color-3.mx-0              
              img.img-a.img-t.mt-3(src='@/assets/curso/temas/c.svg', alt='')
            .col-lg-11.mx-0.p-4
              p.my-3 La recta de regresin simple usa una variable independiente para explicar o predecir el resultado de la variable dependiente Y, mientras que la recta de regresin mltiple usa dos o ms variables independientes para predecir el resultado. 
  

          .row.justify-content-between.align-items-stretch.mt-5.col-color-4     
            .col-lg-1.col-color-3.mx-0              
              img.img-a.img-t.mt-4(src='@/assets/curso/temas/d.svg', alt='')
            .col-lg-11.mx-0.p-4
              p La forma general de cada tipo de regresin es: Frmula de la recta de regresin simple: #[b Y = a + bX + u]
              p Frmula de la recta de regresin mltiple: #[b Y = a + b1X1 + b2 X2 + b3 X3 +  + bt Xt + u  ]

              .row.justify-content-start.align-items-center.mt-4.ps-3     
                .col-lg-2.bg-modal-p3.p-2
                  h3.mb-0.text-white.text-center #[b Donde:]

              p.mt-4 Y = la variable que est intentando predecir (variable dependiente).
              p X = la variable que est utilizando para predecir Y (variable independiente).
              p a = la interseccin.
              p b = la pendiente.
              p u = el residual de regresin.

