      h2 1.4 Muestreo, distribucin de medias muestrales

    p(data-aos="fade-down") Para este tema se invita a  revisar de qu trata el muestreo y la distribucin de medias muestrales.

    .row.justify-content-center.align-items-center.mt-5           
      .col-lg-12(data-aos="fade-left")     
        figure
          .video
            iframe(width="560" height="315" src="https://www.youtube.com/embed/5VK7hvyj760" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen)

    p(data-aos="fade-down").mt-5 Y ahora se sugiere complementar la informacin con la siguiente presentacin.
     
    .row.justify-content-start.align-items-start.mt-5     
      .col-lg-7 
        .row.justify-content-start.align-items-center.ps-3      
          .col-lg-12.green-p.p-3(data-aos="fade-down")
            p(data-aos="fade-down").mb-0.text-white #[b Tipos de muestreo ms comunes]      
        LineaTiempoD.color-acento-contenido.mt-4
          .row(numero="1" titulo="Muestreo aleatorio")
            .col-11
              .row.justify-content-center.align-items-center.mt-5 
                .col-lg-4.my-3
                  img.img-a.img-t(src='@/assets/curso/temas/img83.svg', alt='')           
                .col-lg-8
                  p Con el muestreo aleatorio, cada elemento de una poblacin tiene la misma probabilidad de ser elegido. Es el ms alejado de cualquier sesgo potencial, porque no hay un juicio humano involucrado en la seleccin de la muestra. Por ejemplo, una muestra aleatoria puede incluir elegir los nombres de 25 empleados de un sombrero en una empresa de 250 empleados. La poblacin es de 250 empleados y la muestra es aleatoria porque cada empleado tiene la misma probabilidad de ser elegido.
 
          .row(numero="2" titulo="Muestreo discrecional o de juicio")
            .col-11
              p Se puede utilizar el juicio del auditor para seleccionar la muestra de la poblacin completa. Un auditor solo puede estar preocupado por transacciones de naturaleza material. Por ejemplo, suponga que el auditor establece el umbral de importancia relativa para las transacciones de cuentas por pagar en $10.000. Si el cliente proporciona una lista completa de 15 transacciones de ms de $10,000, el auditor puede optar por revisar todas las transacciones debido al pequeo tamao de la poblacin.
              img.img-a.img-t.mt-4(src='@/assets/curso/temas/img84.svg', alt='') 

          .row(numero="3" titulo="Muestreo en bloque")
            .col-11
              p El muestreo por bloques toma una serie consecutiva de elementos dentro de la poblacin para usar como muestra. Por ejemplo, una lista de todas las transacciones de ventas en un perodo contable podra clasificarse de varias formas, incluso por fecha o por monto en dlares. 
              img.img-a.img-t.mt-4(src='@/assets/curso/temas/img85.svg', alt='') 

              .cajon.color-primario.p-4.mt-4
                p.mb-0 Un auditor puede solicitar que el contador de la empresa proporcione la lista en un formato u otro para seleccionar una muestra de un segmento especfico de la lista. Este mtodo requiere muy poca modificacin por parte del auditor, pero es probable que un bloque de transacciones no sea representativo de la poblacin completa.
                   
              .row.justify-content-center.align-items-center.mt-4 
                .col-lg-4.my-3
                  img.img-a.img-t(src='@/assets/curso/temas/img86.svg', alt='')           
                .col-lg-8
                  p Alternativamente, un auditor puede identificar todas las cuentas del libro mayor con una variacin mayor al 10% del perodo anterior. En este caso, el auditor est limitando la poblacin de la que se deriva la seleccin de la muestra. Desafortunadamente, el juicio humano utilizado en el muestreo siempre conlleva la posibilidad de sesgo, ya sea explcito o implcito. 

          .row(numero="4" titulo="Muestreo sistemtico")
            .col-11
              p Los investigadores utilizan el mtodo de muestreo sistemtico para elegir los miembros de la muestra de una poblacin a intervalos regulares. Requiere la seleccin de un punto de partida para la muestra y el tamao de la muestra que pueda repetirse a intervalos regulares.

              .cajon.color-primario.p-4.mt-4
                p.mb-0 Este tipo de mtodo de muestreo tiene un rango predefinido y, por lo tanto, esta tcnica de muestreo es la que requiere menos tiempo. 

              img.img-a.img-t.my-4(src='@/assets/curso/temas/img87.svg', alt='') 
              
              p Suponga que un auditor est revisando los controles internos relacionados con la cuenta de efectivo de una empresa y desea probar la poltica de la empresa que estipula que los cheques que superen los $ 10,000 deben estar firmados por dos personas. 
              p La poblacin consiste en cada cheque de la compaa que exceda los $ 10,000 durante el ao fiscal, que, en este ejemplo, fue de 300. El auditor usa estadsticas de probabilidad y determina que el tamao de la muestra debe ser el 20% de la poblacin o 60 cheques. El intervalo de muestreo es 5 (300 controles / 60 controles de muestra)
              .row.justify-content-center.align-items-center.mt-4 
                .col-lg-4.my-3
                  img.img-a.img-t(src='@/assets/curso/temas/img88.svg', alt='')           
                .col-lg-8
                  p Por lo tanto, el auditor selecciona una de cada cinco verificaciones para probarlas. Suponiendo que no se encuentran errores en el trabajo de prueba de muestreo, el anlisis estadstico le da al auditor una tasa de confianza del 95% de que el procedimiento de verificacin se realiz correctamente. El auditor prueba la muestra de 60 cheques y no encuentra errores, por lo que concluye que el control interno sobre el efectivo est funcionando correctamente.

          .row(numero="5" titulo="Ejemplo de muestreo de <em>marketing</em>")
            .col-11
              p Las empresas tienen como objetivo vender sus productos y / o servicios a los mercados objetivo. Antes de presentar productos al mercado, las empresas generalmente identifican las necesidades y deseos de su pblico objetivo. Para hacerlo, pueden emplear un muestreo de la poblacin del mercado objetivo para obtener una mejor comprensin de esas necesidades para luego crear un producto y / o servicio que satisfaga esas necesidades. En este caso, recoger las opiniones de la muestra ayuda a identificar las necesidades del conjunto.
      .col-lg-5.my-3
        img.img-a.img-t(src='@/assets/curso/temas/img82.jpg', alt='')
    
    .row.justify-content-start.align-items-center.mt-5(data-aos="fade-left")
      .col-lg-4.bg-modal-p2.p-3
        h3.mb-0.text-white #[b Distribucin de medias muestrales]

    .row.justify-content-center.align-items-center.mt-5         
      .col-lg-4.my-3
        img.img-a.img-t(data-aos="zoom-in")(src='@/assets/curso/temas/img89.svg', alt='')
      .col-lg-8
        p(data-aos="fade-down") La media muestral de un grupo de observaciones es una estimacin de la media poblacional . Dada una muestra de tamao n, considere n variables aleatorias independientes X1, X2, , Xn, cada una de las cuales corresponde a una observacin seleccionada al azar. Cada una de estas variables tiene la distribucin de la poblacin, con media  y desviacin estndar . La media muestral se define como:

        img.img-a.img-t.my-4(data-aos="zoom-in")(src='@/assets/curso/temas/img90.svg', alt='')
        
        p(data-aos="fade-down") Por las propiedades de las medias y las varianzas de las variables aleatorias, la media y la varianza de la media muestral son las siguientes: 

        img.img-a.img-t.my-4(data-aos="zoom-in")(src='@/assets/curso/temas/img91.svg', alt='')

        p(data-aos="fade-down") Aunque la media de la distribucin de  es idntica a la media de la distribucin de la poblacin, la varianza es mucho menor para tamaos de muestra grandes.

    .cajon.color-primario.p-4.mt-5(data-aos="flip-down")
      p(data-aos="fade-down").mb-0 Por ejemplo, suponga que la variable aleatoria X registra el puntaje de un estudiante seleccionado al azar en una prueba nacional, donde la distribucin de la poblacin para el puntaje es normal con una media de 70 y una desviacin estndar de 5 (N (70,5)). Dada una muestra aleatoria simple (SRS) de 200 estudiantes, la distribucin de la puntuacin media de la muestra  tiene una media de 70 y una desviacin estndar de 5 / sqrt (200) = 5 / 14,14 = 0,35.

    .row.justify-content-center.align-items-center.mt-5 
      .col-lg-8
        p(data-aos="fade-down") La distribucin de medias muestrales se define como el conjunto de medias de todas las posibles muestras aleatorias de un tamao especfico (n) seleccionadas de una poblacin especfica. Esta distribucin tiene caractersticas bien definidas (y predecibles) que se especifican en el teorema del Lmite central.

        img.img-a.img-t.my-4(data-aos="zoom-in")(src='@/assets/curso/temas/img93.svg', alt='')  

        p(data-aos="fade-down") Cuando la distribucin de la poblacin es normal, la distribucin de la media muestral tambin es normal. Para una distribucin de poblacin normal con media  y desviacin estndar , la distribucin de la media de la muestra es normal, con media  y desviacin estndar.         
        img.img-a.img-t.mt-4(data-aos="zoom-in")(src='@/assets/curso/temas/img94.svg', alt='') 
      .col-lg-4.my-3
        img.img-a.img-t(data-aos="zoom-in")(src='@/assets/curso/temas/img92.svg', alt='')

    .cajon.color-acento-botones.p-4.mt-5(data-aos="flip-down")
      p(data-aos="fade-down").mb-0 Este resultado se deriva del hecho de que cualquier combinacin lineal de variables aleatorias normales independientes tambin se distribuye normalmente. Esto significa que para dos variables aleatorias normales independientes X, Y y cualquier constante a y b, Ax + By sern distribuidos normalmente. En el caso de la media muestral, la combinacin lineal es = (1 / n) * (X1 + X2 +  Xn).  

    .row.justify-content-start.align-items-center.mt-5(data-aos="fade-left")
      .col-lg-4.bg-modal-p2.p-3
        h3.mb-0.text-white #[b Teorema del Lmite central]

    .row.justify-content-center.align-items-center.mt-5 
      .col-lg-8
        p(data-aos="fade-down") El resultado ms importante sobre las medias muestrales es el teorema del Lmite central. En pocas palabras, este teorema dice que para un tamao de muestra n suficientemente grande, la distribucin de la media muestral X se acercar a una distribucin normal. Esto es cierto para una muestra de variables aleatorias independientes de cualquier distribucin de poblacin, siempre que la poblacin tenga una desviacin estndar finita . Una declaracin formal del teorema del lmite central es la siguiente:

        p(data-aos="fade-down") Si X es la media de una muestra aleatoria X1, X2, , Xn de tamao n de una distribucin con una media finita  y una varianza positiva finita , entonces la distribucin de W =
        img.img-a.img-t(data-aos="zoom-in")(src='@/assets/curso/temas/img96.svg', alt='')        
 
        .row.justify-content-start.align-items-center.ps-3.my-5         
          .col-lg-6.green-p.p-3(data-aos="fade-down")
            p(data-aos="fade-down").mb-0.text-white #[b es N (0,1) en el lmite cuando n se acerca al infinito.]  
        .row.justify-content-start.align-items-end.mt-5 
          .col-lg-5
            p.mb-0 Esto significa que la variable  se distribuye
          .col-auto.p-0
            img(src='@/assets/curso/temas/img97.svg')
        p Una aplicacin bien conocida de este teorema es la aproximacin normal a la distribucin binomial.              
      .col-lg-4.my-3
        img.img-a.img-t(data-aos="zoom-in")(src='@/assets/curso/temas/img95.svg', alt='')  

    img.img-a.img-t.mt-5(data-aos="zoom-in")(src='@/assets/curso/temas/img98.svg', alt='')  

    p(data-aos="fade-down").mt-5 El concepto de distribucin de medias muestrales y sus caractersticas deben ser intuitivamente razonables:

    .row.justify-content-evenly.align-items-stretch.mt-5 
      .col-lg-4.col-bg-1.p-5(data-aos="fade-left")
        img.img-t(data-aos="zoom-in")(src='@/assets/curso/temas/img99.svg', alt='')
        p.mt-4.mb-0 Es de notar que las medias muestrales son variables. Si se seleccionan dos (o ms) muestras de la misma poblacin, es probable que las dos muestras tienen medias diferentes. 

      .col-lg-4.col-bg-2.p-5(data-aos="fade-down")
        img.img-t(data-aos="zoom-in")(src='@/assets/curso/temas/img99.svg', alt='')
        p.mt-4.mb-0 Aunque las muestras tendrn medias diferentes, se debe esperar que las medias muestrales estn cerca de la media de la poblacin. Es decir, las medias de la muestra deberan acumularse alrededor de . Por tanto, la distribucin de las medias muestrales tiende a adoptar una forma normal con un valor esperado de .  

      .col-lg-4.col-bg-3.p-5(data-aos="fade-right")
        img.img-t(data-aos="zoom-in")(src='@/assets/curso/temas/img99.svg', alt='')
        p.mt-4.mb-0 Debe darse cuenta de que la media de una muestra individual probablemente no ser idntica a la media de su poblacin; es decir, habr algn error entre X y . Algunas medias de muestra estarn relativamente cerca de  y otras relativamente lejos. El error estndar proporciona una medida de la distancia estndar entre X y .  

    .row.justify-content-start.align-items-center.mt-5(data-aos="fade-left")
      .col-lg-7.bg-modal-p2.p-3
        h3.mb-0.text-white #[b Puntuaciones Z y ubicacin dentro de la distribucin de medias muestrales]

    .row.justify-content-center.align-items-center
      .col-lg-6.tarjeta-avatar-b.mt-5(data-aos="fade-left")
        .tarjeta-avatar-b__img
          img.m-top(src='@/assets/curso/temas/img100.svg' alt='AvatarTop')
        .tarjeta.color-primario
          .p-4
            p.tex-black Dentro de la distribucin de las medias muestrales, la ubicacin de cada media muestral se puede especificar mediante una puntuacin Z,
            img.img-t.img-r.my-3(data-aos="zoom-in")(src='@/assets/curso/temas/img101.svg', alt='')

      .col-lg-6.tarjeta-avatar-b.mt-5(data-aos="fade-right")
        .tarjeta-avatar-b__img
          img.m-top(src='@/assets/curso/temas/img100.svg' alt='AvatarTop')
        .tarjeta.color-primario
          .p-4
            p.tex-black Como siempre, una puntuacin Z positiva indica una media muestral mayor que  y una puntuacin Z negativa corresponde a una media muestral menor que .

    .row.justify-content-start.align-items-center.mt-5(data-aos="fade-right")
      .col-lg-10.green-p.p-3
        h3.mb-0.text-white #[b El valor numrico de la puntuacin Z indica la distancia entre  y  medida en trminos del error estndar.]

    Separador 
    #t_1_5.titulo-segundo.color-acento-contenido(data-aos="zoom-in-left")
      h2 1.5 Estimacin y prueba de hiptesis 

    .bloque-texto-g.color-secundario.p-3.p-sm-4.p-md-5.mt-5(data-aos="fade-left")
      .bloque-texto-g__img(
        :style="{'background-image': `url(${require('@/assets/curso/temas/img102.jpg')})`}"
      )
      .bloque-texto-g__texto.p-4
        p.mb-0 Un rea de preocupacin en la estadstica inferencial es la estimacin del parmetro de poblacin a partir de la estadstica de la muestra; aqu es importante darse cuenta del orden. La estadstica de muestra se calcula a partir de los datos de muestra y el parmetro de poblacin se infiere (o estima) a partir de esta estadstica de muestra: 

    p(data-aos="fade-down").mt-5 Otra rea de la estadstica inferencial es la determinacin del tamao de la muestra. Es decir, qu tamao de muestra debe tomarse para hacer una estimacin precisa. En estos casos, las estadsticas no se pueden utilizar, ya que an no se ha tomado la muestra.

    .row.justify-content-center.align-items-center.mt-5           
      .col-lg-7
        LineaTiempoD.color-acento-contenido
          .row.justify-content-center.align-items-center.pb-4(numero="1" titulo="Prueba de hiptesis")
            .col-11
              p Hay dos tipos de estimaciones que encontraremos: estimaciones puntuales y estimaciones de intervalo. La estimacin puntual es el mejor valor individual. Un buen estimador debe satisfacer tres condiciones:
              .row.mt-4
                .col-lg-12.tarjeta-avatar-b
                  .tarjeta-avatar-b__img.mr
                    img.img-w(src='@/assets/curso/temas/img100.svg' alt='AvatarTop')
                  .tarjeta.color-primario.bg-avatar1
                    .p-4.ps-1
                      p.mb-0 #[b Insesgado:] el valor esperado del estimador debe ser igual a la media del parmetro.
              .row.mt-4
                .col-lg-12.tarjeta-avatar-b
                  .tarjeta-avatar-b__img.mr
                    img.img-w(src='@/assets/curso/temas/img100.svg' alt='AvatarTop')
                  .tarjeta.color-primario.bg-avatar2
                    .p-4.ps-1
                      p.mb-0 #[b Consistente:] el valor del estimador se acerca al valor del parmetro a medida que aumenta el tamao de la muestra.
              .row.mt-4
                .col-lg-12.tarjeta-avatar-b
                  .tarjeta-avatar-b__img.mr
                    img.img-w(src='@/assets/curso/temas/img100.svg' alt='AvatarTop')
                  .tarjeta.color-primario.bg-avatar3
                    .p-4.ps-1
                      p.mb-0 #[b Relativamente eficiente:] el estimador tiene la varianza ms pequea de todos los estimadores que podran usarse.

          .row.justify-content-center.align-items-center.pb-4(numero="2" titulo="Intervalos de confianza")
            .col-11
              p La estimacin puntual va a ser diferente del parmetro de poblacin porque debido al error de muestreo, y no hay forma de saber quin se acerca al parmetro real. Por esta razn, a los estadsticos les gusta dar una estimacin de intervalo que es un rango de valores utilizados para estimar el parmetro. Un intervalo de confianza es una estimacin de intervalo con un nivel de confianza especfico. Un nivel de confianza es la probabilidad de que la estimacin del intervalo contenga el parmetro. El nivel de confianza es 1-alfa y esta se encuentra dentro del intervalo de confianza.
                
          .row.justify-content-center.align-items-center.pb-4(numero="3" titulo="Error mximo de la estimacin")
            .col-11
              p El error mximo de la estimacin se indica con E y es la mitad del ancho del intervalo de confianza. El intervalo de confianza bsico para una distribucin simtrica se establece para que sea la estimacin puntual menos el error mximo de la estimacin es menor que el parmetro de poblacin real que es menor que la estimacin puntual ms el error mximo de la estimacin. Esta frmula funcionar para medias y proporciones, porque usarn las distribuciones Z o T que son simtricas.
 
          .row.justify-content-center.align-items-center.pb-4(numero="4" titulo="Prueba de hiptesis")
            .col-11
              p Cuando evalas una hiptesis, debes tener en cuenta tanto la variabilidad de tu muestra como el tamao de la muestra. La prueba de hiptesis se usa generalmente cuando se comparan dos o ms grupos. Por ejemplo, puede implementar protocolos para realizar la intubacin en pacientes peditricos en el entorno prehospitalario. Para evaluar si estos protocolos tuvieron xito en mejorar las tasas de intubacin, podra medir la tasa de intubacin a lo largo del tiempo en un grupo asignado aleatoriamente a la capacitacin en los nuevos protocolos y compararla con la tasa de intubacin a lo largo del tiempo en otro grupo de control que no recibi capacitacin en los nuevos protocolos.
              img.img-t.img-a(data-aos="zoom-in")(src='@/assets/curso/temas/img104.svg', alt='')
              .row.justify-content-start.align-items-center.mt-5
                .col-lg-8
                  .cajon.color-primario.p-4.mt-4
                    p.mb-0 Cuando evalas una hiptesis, debes tener en cuenta tanto la variabilidad de tu muestra como el tamao de la muestra.  Con base en esta informacin, le gustara hacer una evaluacin de si las diferencias que ve son significativas o si es probable que se deba a la casualidad. Esto se hace formalmente a travs de un proceso llamado prueba de hiptesis.
                .col-lg-4
                  img.img-t.img-a(src='@/assets/curso/temas/img105.svg', alt='')
  
      .col-lg-5
        img.img-a.img-t(src='@/assets/curso/temas/img19.svg', alt='')

    p(data-aos="fade-down").mt-5 Cinco pasos en la prueba de hiptesis:
    .row.justify-content-center.align-items-center.mt-5           
      .col-lg-10
        .row.justify-content-evenly.align-items-stretch        
          .col-lg-2.bg-modal.p-3.my-3(data-aos="fade-left")
            img.img-a.img-t(data-aos="zoom-in")(src='@/assets/curso/temas/img108.svg', alt='')
            p.text-center.mb-0.mt-2 #[b Especificar la hiptesis nula]
          .col-lg-2.bg-modal.p-3.my-3(data-aos="fade-left")
            img.img-a.img-t(data-aos="zoom-in")(src='@/assets/curso/temas/img109.svg', alt='')
            p.text-center.mb-0.mt-2 #[b Especificar la hiptesis alternativa]
          .col-lg-2.bg-modal.p-3.my-3(data-aos="fade-down")        
            img.img-a.img-t(data-aos="zoom-in")(src='@/assets/curso/temas/img110.svg', alt='')
            p.text-center.mb-0.mt-2 #[b Establecer el nivel de significacin ()]
          .col-lg-2.bg-modal.p-3.my-3(data-aos="fade-right")       
            img.img-a.img-t(data-aos="zoom-in")(src='@/assets/curso/temas/img111.svg', alt='')
            p.text-center.mb-0.mt-2 #[b Calcule la estadstica de prueba y el valor P correspondiente]
          .col-lg-2.bg-modal.p-3.my-3(data-aos="fade-right")      
            img.img-a.img-t(data-aos="zoom-in")(src='@/assets/curso/temas/img112.svg', alt='')
            p.text-center.mb-0.mt-2 #[b Esbozando una conclusin]          

    p(data-aos="fade-down").mt-5 Para ampliar la informacin sobre estos importantes pasos en la estimacin y la prueba de hiptesis, se invita a revisar:

    TabsA.color-secundario.mt-5(data-aos="fade-left")
      .tarjeta.color-secundario--borde.p-4(titulo="Especifique la hiptesis nula")    
        .row.justify-content-center.align-items-center        
          .col-lg-11
            h5 Especifique la hiptesis nula
            p La hiptesis nula (H0) es una declaracin de ningn efecto, relacin o diferencia entre dos o ms grupos o factores. En los estudios de investigacin, un investigador suele estar interesado en refutar la hiptesis nula.

            .row.justify-content-start.align-items-center.mt-4
              .col-lg-2.green-p.p-2
                h3.mb-0.text-white #[b Ejemplos] 
            .row.justify-content-center.align-items-center.mt-4
              .col-lg-11
                ul.lista-ul--color
                  li 
                    i.fas.fa-caret-right
                    p.mb-0 No hay diferencia en las tasas de intubacin entre las edades de 0 a 5 aos.
                  li 
                    i.fas.fa-caret-right
                    p.mb-0 Los grupos de intervencin y control tienen la misma tasa de supervivencia (o la intervencin no mejora la tasa de supervivencia).
                  li 
                    i.fas.fa-caret-right
                    p.mb-0 No existe asociacin entre el tipo de lesin y si el paciente recibi o no una va intravenosa en el entorno prehospitalario.

     
      .tarjeta.color-secundario--borde.p-4(titulo="Especifique la hiptesis alternativa")    
        .row.justify-content-center.align-items-center        
          .col-lg-11
            h5 Especifique la hiptesis alternativa
            p La hiptesis alternativa (HA) es la afirmacin de que existe un efecto o diferencia. Esta suele ser la hiptesis que el investigador est interesado en probar. La hiptesis alternativa puede ser unilateral (solo proporciona una direccin, por ejemplo, inferior) o bilateral. A menudo, utilizamos pruebas de dos caras incluso cuando nuestra verdadera hiptesis es unilateral, porque requiere ms evidencia en contra de la hiptesis nula para aceptar la hiptesis alternativa.
            .row.justify-content-start.align-items-center.mt-4
              .col-lg-2.green-p.p-2
                h3.mb-0.text-white #[b Ejemplos] 
            .row.justify-content-center.align-items-center.mt-4
              .col-lg-11
                ul.lista-ul--color
                  li 
                    i.fas.fa-caret-right
                    p.mb-0 La tasa de xito de la intubacin difiere segn la edad del paciente tratado (bilateral).
                  li 
                    i.fas.fa-caret-right
                    p.mb-0 El tiempo hasta la reanimacin de un paro cardaco es menor para el grupo de intervencin que para el control (unilateral).
                  li 
                    i.fas.fa-caret-right
                    p.mb-0 Existe una asociacin entre el tipo de lesin y si el paciente recibi o no una va intravenosa en el entorno prehospitalario (bilateral).
 
      .tarjeta.color-secundario--borde.p-4(titulo="Establecer el nivel ")    
        .row.justify-content-center.align-items-center        
          .col-lg-11
            h5 Establecer el nivel de significancia ()
            p El nivel de significancia (denotado por la letra griega alfa ) generalmente se establece en 0,05. Esto significa que hay 5% de probabilidad de que acepte su hiptesis alternativa cuando su hiptesis nula sea realmente cierta. Cuanto menor sea el nivel de significancia, mayor ser la carga de la prueba necesaria para rechazar la hiptesis nula o, en otras palabras, para respaldar la hiptesis alternativa.
            img.img-a.img-t.mt-3(src='@/assets/curso/temas/img113.svg', alt='')

      .tarjeta.color-secundario--borde.p-4(titulo="Calcule la estadstica")    
        .row.justify-content-center.align-items-center        
          .col-lg-11
            h5 Calcule la estadstica de prueba y el valor P correspondiente
            p En otra seccin presentamos algunas estadsticas de prueba bsicas para evaluar una hiptesis. La prueba de hiptesis generalmente utiliza una estadstica de prueba que compara grupos o examina asociaciones entre variables. Cuando se describe una sola muestra sin establecer relaciones entre variables, se suele utilizar un intervalo de confianza.
            .cajon.color-primario.p-4.mt-4
              p.mb-0 El valor p describe la probabilidad de obtener un estadstico de muestra tan o ms extremo por casualidad solo si su hiptesis nula es cierta. Este valor p se determina en funcin del resultado de su estadstica de prueba. Sus conclusiones sobre la hiptesis se basan en su valor p y su nivel de significancia.           
            .row.justify-content-start.align-items-center.mt-4
              .col-lg-2.green-p.p-2
                h3.mb-0.text-white #[b Ejemplos] 
