              .col-lg-11
                ul.lista-ul--color
                  li 
                    i.fas.fa-caret-right
                    p.mb-0 #[b Valor p = 0,01.] Esto suceder 1 de cada 100 veces por pura casualidad si su hiptesis nula es cierta. No es probable que suceda estrictamente por casualidad.
                  li 
                    i.fas.fa-caret-right
                    p.mb-0 #[b Valor p = 0,75.] Esto suceder 75 de cada 100 veces por pura casualidad si su hiptesis nula es cierta. Es muy probable que ocurra estrictamente por casualidad.

    .row.justify-content-start.align-items-center.mt-5(data-aos="fade-right")
