        h3.mb-0.text-white #[b Precauciones sobre los valores p]
    p(data-aos="fade-down").mt-5 El tamao de la muestra afecta directamente su valor p. Los tamaos de muestra grandes producen valores p pequeos incluso cuando las diferencias entre los grupos no son significativas. Siempre debe verificar la relevancia prctica de sus resultados. Por otro lado, un tamao de muestra demasiado pequeo puede provocar que no se identifique una diferencia cuando realmente existe. 

    .bloque-texto-g.color-acento-contenido.p-3.p-sm-4.p-md-5.mt-5(data-aos="fade-down")
      .bloque-texto-g__img(
