      )
      .bloque-texto-g__texto.p-4
        p.mb-0 Se debe planificar el tamao de la muestra con anticipacin para tener  suficiente informacin de su muestra para mostrar una relacin o diferencia significativa, si existe. Consulte el clculo de un tamao de muestra para obtener ms informacin. 

    .row.justify-content-center.align-items-stretch.mt-5     
