        p.mt-4(data-aos="fade-down") #[b Ejemplo 1]
        p.mb-0(data-aos="fade-down") Las edades medias fueron significativamente diferentes entre los dos grupos (16,2 aos frente a 16,7 aos; p = 0,01; n = 1.000). Es esta una diferencia importante? Probablemente no, pero el gran tamao de la muestra ha dado como resultado un valor p pequeo.

      .col-lg-6.col-bg-2.p-5(data-aos="fade-right")
        img.img-t(src='@/assets/curso/temas/img115.svg', alt='')
        p.mt-4(data-aos="fade-down") #[b Ejemplo 2]
        p.mb-0(data-aos="fade-down") Las edades medias no fueron significativamente diferentes entre los dos grupos (10,4 aos frente a 16,7 aos; p = 0,40, n = 10). Es esta una diferencia importante? Podra ser, pero debido a que el tamao de la muestra es pequeo, no se puede determinar con certeza si se trata de una verdadera diferencia o simplemente sucedi debido a la variabilidad natural en la edad dentro de estos dos grupos. 

    p(data-aos="fade-down").mt-5 Si realiza una gran cantidad de pruebas para evaluar una hiptesis (lo que se denomina prueba mltiple), debe controlar esto en su designacin del nivel de significancia o en el clculo del valor p. Por ejemplo, si tres resultados miden la efectividad de un frmaco u otra intervencin, tendr que ajustar estos tres anlisis.

