      .row.m-0.align-items-center.justify-content-between
        .col-lg-12
          .bloque-texto-a__texto.p-4
            p.mb-0(data-aos="fade-down") La prueba de hiptesis no est configurada para que pueda probar absolutamente una hiptesis nula. Por lo tanto, cuando no se encuentra evidencia en contra de la hiptesis nula, no se puede rechazar la hiptesis nula. Cuando se encuentra evidencia suficientemente fuerte en contra de la hiptesis nula, se rechaza la hiptesis nula. Sus conclusiones tambin se traducen en una declaracin sobre su hiptesis alternativa. Cuando presente los resultados de una prueba de hiptesis, incluya tambin las estadsticas descriptivas en sus conclusiones. Informe valores p exactos en lugar de un rango determinado. Por ejemplo, La tasa de intubacin difiri significativamente segn la edad del paciente y los pacientes ms jvenes tienen una tasa ms baja de intubacin exitosa (p = 0,02). Aqu hay dos ejemplos ms con la conclusin expresada de varias formas diferentes. 

