      .bloque-texto-g__texto.p-4
        p.mb-0 En la teora de la probabilidad, la probabilidad condicionada o condicional es una medida de la probabilidad de que ocurra un evento, dado que ya ha ocurrido otro evento (por suposicin, presuncin, afirmacin o evidencia). Veamos:  

    .row.justify-content-center.align-items-center.mt-5
      .col-lg-8
        p(data-aos="fade-down") Si el evento de inters es A y se sabe o se supone que ocurri el evento B, la probabilidad condicional de A dado B , o la probabilidad de A bajo la condicin B , generalmente se escribe como P (A | B) y ocasionalmente PB(A). Esto tambin se puede entender como la fraccin de probabilidad B que se cruza con A: 
      .col-lg-4
        img.img-t.img-a.my-3(data-aos="zoom-in")(src='@/assets/curso/temas/img147.svg', alt='')

    .row.justify-content-start.align-items-center.mt-5       
      .col-lg-2.green-p.p-3(data-aos="fade-down")
        p(data-aos="fade-down").mb-0.text-white #[b Por ejemplo] 

    .row.justify-content-center.align-items-center.mt-5
      .col-lg-8
        p(data-aos="fade-down") La probabilidad de que una persona determinada tenga tos en un da determinado puede ser solo del 5%. Pero si sabemos o asumimos que la persona est enferma, es mucho ms probable que est tosiendo. Por ejemplo, la probabilidad condicional de que alguien no est bien tosiendo podra ser del 75%, en cuyo caso tendramos que P (Tos) = 5% y P (Tos | Enfermo) = 75%. Aunque, no tiene que haber relacin o dependencia entre A y B, y no tienen que ocurrir simultneamente. 
        .row.justify-content-start.align-items-center.mt-5       
          .col-lg-4.green-p.p-3(data-aos="fade-down")
            p(data-aos="fade-down").mb-0.text-white #[b Teniendo en cuenta que ]
        p(data-aos="fade-down").mt-5 P (A | B) puede o no ser igual a P (A) (la probabilidad incondicional de A). Si P (A | B) = P (A), entonces se dice que los eventos A y B son independientes: en tal caso, el conocimiento sobre cualquiera de los eventos no altera la probabilidad de los dems. P (A | B) (la probabilidad condicional de A dado B) tpicamente difiere de P (B | A). 

      .col-lg-4
        img.img-t.img-a.my-3(data-aos="zoom-in")(src='@/assets/curso/temas/img148.svg', alt='')

    .row.justify-content-center.align-items-center
      .col-lg-4
        img.img-t.img-a.my-3(data-aos="zoom-in")(src='@/assets/curso/temas/img149.svg', alt='')    
      .col-lg-8
        .row.justify-content-start.align-items-center.mt-4       
          .col-lg-2.green-p.p-3(data-aos="fade-down")
            p(data-aos="fade-down").mb-0.text-white #[b Por ejemplo]      
        p(data-aos="fade-down").mt-4 Si una persona tiene fiebre del dengue, podra tener 90% de probabilidades de dar positivo en la prueba de la enfermedad. En este caso, lo que se est midiendo es que si ha ocurrido el evento B (tener dengue), la probabilidad de A (resultado positivo) dado que B ocurri es del 90%: P (A | B) = 90%. Alternativamente, si una persona da positivo en la prueba del dengue, es posible que solo tenga 15% de probabilidades de tener esta rara enfermedad debido a las altas tasas de falsos positivos. 

        .row.justify-content-start.align-items-center.mt-4       
          .col-lg-2.green-p.p-3(data-aos="fade-down")
            p(data-aos="fade-down").mb-0.text-white #[b En este caso]
        p(data-aos="fade-down").mt-4 La probabilidad del evento B (tener dengue) dado que el evento A (resultado positivo) que ha ocurrido es 15%: P (B | A) = 15% Ahora debera ser evidente que igualar falsamente las dos probabilidades puede conducir a varios errores de razonamiento, lo que comnmente se ve a travs de falacias de tasa base.

    .row.justify-content-center.align-items-center.mt-2 
      .col-lg-8
        .row.justify-content-start.align-items-center.mt-5       
          .col-lg-11.green-p.p-3(data-aos="fade-down")
            p(data-aos="fade-down").mb-0.text-white #[b Si bien las probabilidades condicionales pueden proporcionar informacin extremadamente til]      
        p(data-aos="fade-down").mt-4 A menudo se proporciona o se dispone de informacin limitada. Por lo tanto, puede ser til revertir o convertir una probabilidad de condicin usando el teorema de Bayes:

        img.img-t.img-a.mt-4(data-aos="zoom-in")(src='@/assets/curso/temas/img150.svg', alt='')   

        p(data-aos="fade-down").mt-4 Otra opcin es mostrar probabilidades condicionales en una tabla de probabilidad condicional para iluminar la relacin entre eventos.

      .col-lg-4
        img.img-t.img-a.my-3(data-aos="zoom-in")(src='@/assets/curso/temas/img151.svg', alt='')   

    Separador 
    #t_1_9.titulo-segundo.color-acento-contenido(data-aos="zoom-in-left")
      h2 1.9 Combinatoria: variaciones, permutaciones y combinaciones 

    .row.justify-content-center.align-items-center.mt-2 
