        img.img-t.img-a.my-3(data-aos="zoom-in")(src='@/assets/curso/temas/img152.svg', alt='') 
      .col-lg-8
        p(data-aos="fade-down") La combinatoria se ocupa de conjuntos finitos y sus objetos bsicos son permutaciones, variaciones y combinaciones (y se refieren tanto a las operaciones como a los resultados de estas operaciones). Dado que siempre es posible identificar m y n elementos conjuntos con los conjuntos {1, 2, , m} y {1, 2, , n}, es suficiente considerar estos conjuntos, y esta situacin es una a uno estndar. Recordamos estas tres nociones (en sus dos apariencias: cundo se permiten las repeticiones y cundo no). 

    p(data-aos="fade-down") Esto se hace comenzando con variaciones sin repeticiones (validando esta decisin por una relacin directa con la cardinalidad de todos los mapas finitos), aunque se podra comenzar con combinaciones o con permutaciones. Al cumplir con la permutacin, hay que prestar una especial atencin a las operaciones con factorial.

